import React, { useState } from 'react';

function TaskCreation() {
  const [taskName, setTaskName] = useState('');
  const [category, setCategory] = useState('');
  const [tasks, setTasks] = useState([]);
  
  const handleTaskNameChange = (event) => {
    setTaskName(event.target.value);
  };

  const handleCategoryChange = (event) => {
    setCategory(event.target.value);
  };

  const handleTaskCreation = () => {
    if (taskName.trim() === '' || category.trim() === '') {
      alert('Please enter both task name and category.');
      return;
    }

    // Create a new task object
    const newTask = {
      id: Date.now(), // Generate a unique ID (you might use a library for this)
      name: taskName,
      category: category,
    };

    // Add the new task to the tasks array
    setTasks([...tasks, newTask]);

    // Clear the input fields
    setTaskName('');
    setCategory('');
  };

  return (
    <div>
      <h2>Create and Organize Tasks</h2>
      <div>
        <label>Task Name:</label>
        <input
          type="text"
          value={taskName}
          onChange={handleTaskNameChange}
        />
      </div>
      <div>
        <label>Category:</label>
        <input
          type="text"
          value={category}
          onChange={handleCategoryChange}
        />
      </div>
      <button onClick={handleTaskCreation}>Create Task</button>
      
      {/* Display the list of created tasks */}
      <div>
        <h3>Task List:</h3>
        <ul>
          {tasks.map((task) => (
            <li key={task.id}>
              <strong>{task.name}</strong> - {task.category}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default TaskCreation;
