import React, { useState } from 'react';

function TaskProgress() {
  const [tasks, setTasks] = useState([]);
  const [taskName, setTaskName] = useState('');
  const [status, setStatus] = useState('To Do');
  const [timeSpent, setTimeSpent] = useState(0);

  const handleTaskNameChange = (event) => {
    setTaskName(event.target.value);
  };

  const handleStatusChange = (event) => {
    setStatus(event.target.value);
  };

  const handleTimeSpentChange = (event) => {
    setTimeSpent(Number(event.target.value));
  };

  const handleTaskCreation = () => {
    if (taskName.trim() === '') {
      alert('Please enter a task name.');
      return;
    }

    // Create a new task object
    const newTask = {
      id: Date.now(), // Generate a unique ID (you might use a library for this)
      name: taskName,
      status: status,
      timeSpent: timeSpent,
    };

    // Add the new task to the tasks array
    setTasks([...tasks, newTask]);

    // Clear the input fields
    setTaskName('');
    setStatus('To Do');
    setTimeSpent(0);
  };

  const handleTaskStatusUpdate = (taskId, newStatus) => {
    const updatedTasks = tasks.map((task) => {
      if (task.id === taskId) {
        return { ...task, status: newStatus };
      }
      return task;
    });

    setTasks(updatedTasks);
  };

  const handleTimeTracking = (taskId) => {
    const updatedTasks = tasks.map((task) => {
      if (task.id === taskId) {
        return { ...task, timeSpent: task.timeSpent + timeSpent };
      }
      return task;
    });

    setTasks(updatedTasks);
  };

  return (
    <div>
      <h2>Task Progress Tracking</h2>
      <div>
        <label>Task Name:</label>
        <input
          type="text"
          value={taskName}
          onChange={handleTaskNameChange}
        />
      </div>
      <div>
        <label>Status:</label>
        <select value={status} onChange={handleStatusChange}>
          <option value="To Do">To Do</option>
          <option value="In Progress">In Progress</option>
          <option value="Completed">Completed</option>
        </select>
      </div>
      <div>
        <label>Time Spent (hours):</label>
        <input
          type="number"
          value={timeSpent}
          onChange={handleTimeSpentChange}
        />
      </div>
      <button onClick={handleTaskCreation}>Create Task</button>

      {/* Display the list of created tasks */}
      <div>
        <h3>Task List:</h3>
        <ul>
          {tasks.map((task) => (
            <li key={task.id}>
              <strong>{task.name}</strong> - Status: {task.status}, Time Spent: {task.timeSpent} hours
              <button onClick={() => handleTaskStatusUpdate(task.id, 'To Do')}>To Do</button>
              <button onClick={() => handleTaskStatusUpdate(task.id, 'In Progress')}>In Progress</button>
              <button onClick={() => handleTaskStatusUpdate(task.id, 'Completed')}>Completed</button>
              <button onClick={() => handleTimeTracking(task.id)}>Track Time</button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default TaskProgress;
